import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";
import { Heart, User, Calendar, Phone, Mail, MapPin, Shield, CheckCircle, XCircle, Clock } from "lucide-react";
import { Link } from "react-router-dom";
import { toast } from "sonner";

const PatientProfile = () => {
    const [isEditing, setIsEditing] = useState(false);
    const [formData, setFormData] = useState({
        // User model fields
        email: "patient@example.com",
        role: "patient",
        phoneNumber: "+1 (555) 123-4567",
        location: "New York, NY",
        // Patient model fields
        fullname: "John Doe",
        dateOfBirth: "1990-01-15",
        isKycSubmitted: true,
        isKycApproved: false,
    });

    const handleInputChange = (field: string, value: string) => {
        setFormData(prev => ({ ...prev, [field]: value }));
    };

    const handleSave = () => {
        toast.success("Profile updated successfully!");
        setIsEditing(false);
    };

    const handleCancel = () => {
        setIsEditing(false);
        // Reset form data to original values
        toast.info("Changes discarded");
    };

    const getKycStatusBadge = () => {
        if (!formData.isKycSubmitted) {
            return <Badge variant="secondary" className="bg-gray-100 text-gray-700"><Clock className="h-3 w-3 mr-1" />Not Submitted</Badge>;
        }
        if (formData.isKycSubmitted && !formData.isKycApproved) {
            return <Badge variant="secondary" className="bg-yellow-100 text-yellow-800"><Clock className="h-3 w-3 mr-1" />Pending Approval</Badge>;
        }
        return <Badge className="bg-green-100 text-green-800"><CheckCircle className="h-3 w-3 mr-1" />Approved</Badge>;
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-green-50 to-white">
            {/* Header */}
            <header className="bg-white shadow-sm border-b border-green-100">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
                    <div className="flex items-center justify-between">
                        <Link to="/" className="flex items-center space-x-2">
                            <Heart className="h-8 w-8 text-green-600" />
                            <h1 className="text-2xl font-bold text-gray-900">MedConnect</h1>
                        </Link>
                        <div className="flex space-x-4">
                            <Link to="/patient">
                                <Button variant="outline" className="border-green-200 text-green-700 hover:bg-green-50">
                                    Back to Portal
                                </Button>
                            </Link>
                        </div>
                    </div>
                </div>
            </header>

            {/* Main Content */}
            <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                <div className="mb-8">
                    <h2 className="text-3xl font-bold text-gray-900 mb-2">Patient Profile</h2>
                    <p className="text-gray-600">Manage your personal and medical information</p>
                </div>

                <div className="grid gap-6">
                    {/* Profile Overview Card */}
                    <Card className="border-green-100 shadow-lg">
                        <CardHeader className="bg-green-50 border-b border-green-100">
                            <div className="flex items-center justify-between">
                                <div className="flex items-center space-x-4">
                                    <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center">
                                        <User className="h-8 w-8 text-green-600" />
                                    </div>
                                    <div>
                                        <CardTitle className="text-green-800 text-2xl">{formData.fullname}</CardTitle>
                                        <CardDescription className="text-green-600">Patient ID: #P001</CardDescription>
                                    </div>
                                </div>
                                <div className="flex items-center space-x-2">
                                    {getKycStatusBadge()}
                                    <Shield className="h-5 w-5 text-green-600" />
                                </div>
                            </div>
                        </CardHeader>
                    </Card>

                    {/* Personal Information Card */}
                    <Card className="border-green-100 shadow-lg">
                        <CardHeader className="flex flex-row items-center justify-between">
                            <div>
                                <CardTitle className="text-green-800">Personal Information</CardTitle>
                                <CardDescription>Your basic personal details</CardDescription>
                            </div>
                            <Button
                                onClick={() => isEditing ? handleCancel() : setIsEditing(true)}
                                variant="outline"
                                className="border-green-200 text-green-700 hover:bg-green-50"
                            >
                                {isEditing ? "Cancel" : "Edit Profile"}
                            </Button>
                        </CardHeader>
                        <CardContent className="space-y-6">
                            <div className="grid md:grid-cols-2 gap-6">
                                <div>
                                    <Label htmlFor="fullname" className="text-gray-700 font-medium">Full Name</Label>
                                    {isEditing ? (
                                        <Input
                                            id="fullname"
                                            value={formData.fullname}
                                            onChange={(e) => handleInputChange("fullname", e.target.value)}
                                            className="mt-1 border-green-200 focus:ring-green-500 focus:border-green-500"
                                        />
                                    ) : (
                                        <div className="mt-1 p-3 bg-gray-50 rounded-md border">
                                            <div className="flex items-center">
                                                <User className="h-4 w-4 text-gray-400 mr-2" />
                                                {formData.fullname}
                                            </div>
                                        </div>
                                    )}
                                </div>

                                <div>
                                    <Label htmlFor="dateOfBirth" className="text-gray-700 font-medium">Date of Birth</Label>
                                    {isEditing ? (
                                        <Input
                                            id="dateOfBirth"
                                            type="date"
                                            value={formData.dateOfBirth}
                                            onChange={(e) => handleInputChange("dateOfBirth", e.target.value)}
                                            className="mt-1 border-green-200 focus:ring-green-500 focus:border-green-500"
                                        />
                                    ) : (
                                        <div className="mt-1 p-3 bg-gray-50 rounded-md border">
                                            <div className="flex items-center">
                                                <Calendar className="h-4 w-4 text-gray-400 mr-2" />
                                                {new Date(formData.dateOfBirth).toLocaleDateString('en-US', {
                                                    year: 'numeric',
                                                    month: 'long',
                                                    day: 'numeric'
                                                })}
                                            </div>
                                        </div>
                                    )}
                                </div>

                                <div>
                                    <Label htmlFor="email" className="text-gray-700 font-medium">Email Address</Label>
                                    {isEditing ? (
                                        <Input
                                            id="email"
                                            type="email"
                                            value={formData.email}
                                            onChange={(e) => handleInputChange("email", e.target.value)}
                                            className="mt-1 border-green-200 focus:ring-green-500 focus:border-green-500"
                                        />
                                    ) : (
                                        <div className="mt-1 p-3 bg-gray-50 rounded-md border">
                                            <div className="flex items-center">
                                                <Mail className="h-4 w-4 text-gray-400 mr-2" />
                                                {formData.email}
                                            </div>
                                        </div>
                                    )}
                                </div>

                                <div>
                                    <Label htmlFor="phoneNumber" className="text-gray-700 font-medium">Phone Number</Label>
                                    {isEditing ? (
                                        <Input
                                            id="phoneNumber"
                                            value={formData.phoneNumber}
                                            onChange={(e) => handleInputChange("phoneNumber", e.target.value)}
                                            className="mt-1 border-green-200 focus:ring-green-500 focus:border-green-500"
                                        />
                                    ) : (
                                        <div className="mt-1 p-3 bg-gray-50 rounded-md border">
                                            <div className="flex items-center">
                                                <Phone className="h-4 w-4 text-gray-400 mr-2" />
                                                {formData.phoneNumber}
                                            </div>
                                        </div>
                                    )}
                                </div>

                                <div>
                                    <Label htmlFor="location" className="text-gray-700 font-medium">Location</Label>
                                    {isEditing ? (
                                        <Input
                                            id="location"
                                            value={formData.location}
                                            onChange={(e) => handleInputChange("location", e.target.value)}
                                            className="mt-1 border-green-200 focus:ring-green-500 focus:border-green-500"
                                        />
                                    ) : (
                                        <div className="mt-1 p-3 bg-gray-50 rounded-md border">
                                            <div className="flex items-center">
                                                <MapPin className="h-4 w-4 text-gray-400 mr-2" />
                                                {formData.location}
                                            </div>
                                        </div>
                                    )}
                                </div>

                                <div>
                                    <Label htmlFor="role" className="text-gray-700 font-medium">Role</Label>
                                    <div className="mt-1 p-3 bg-gray-50 rounded-md border">
                                        <div className="flex items-center">
                                            <User className="h-4 w-4 text-gray-400 mr-2" />
                                            <Badge className="bg-green-100 text-green-800 capitalize">{formData.role}</Badge>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            {isEditing && (
                                <div className="flex space-x-4 pt-4">
                                    <Button
                                        onClick={handleSave}
                                        className="medical-gradient text-white hover:opacity-90 flex-1"
                                    >
                                        Save Changes
                                    </Button>
                                    <Button
                                        onClick={handleCancel}
                                        variant="outline"
                                        className="border-gray-300 text-gray-700 hover:bg-gray-50 flex-1"
                                    >
                                        Cancel
                                    </Button>
                                </div>
                            )}
                        </CardContent>
                    </Card>

                    {/* KYC Status Card */}
                    <Card className="border-green-100 shadow-lg">
                        <CardHeader>
                            <CardTitle className="text-green-800 flex items-center">
                                <Shield className="h-5 w-5 mr-2" />
                                KYC Verification Status
                            </CardTitle>
                            <CardDescription>Know Your Customer verification details</CardDescription>
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-4">
                                <div className="flex items-center justify-between p-4 bg-green-50 rounded-lg">
                                    <div className="flex items-center space-x-3">
                                        <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center">
                                            {formData.isKycSubmitted ? (
                                                <CheckCircle className="h-5 w-5 text-green-600" />
                                            ) : (
                                                <Clock className="h-5 w-5 text-gray-400" />
                                            )}
                                        </div>
                                        <div>
                                            <h4 className="font-semibold text-gray-900">KYC Submission</h4>
                                            <p className="text-sm text-gray-600">
                                                {formData.isKycSubmitted ? "Documents submitted" : "Pending submission"}
                                            </p>
                                        </div>
                                    </div>
                                    {getKycStatusBadge()}
                                </div>

                                <div className="flex items-center justify-between p-4 bg-green-50 rounded-lg">
                                    <div className="flex items-center space-x-3">
                                        <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center">
                                            {formData.isKycApproved ? (
                                                <CheckCircle className="h-5 w-5 text-green-600" />
                                            ) : (
                                                <XCircle className="h-5 w-5 text-red-500" />
                                            )}
                                        </div>
                                        <div>
                                            <h4 className="font-semibold text-gray-900">KYC Approval</h4>
                                            <p className="text-sm text-gray-600">
                                                {formData.isKycApproved ? "Verification approved" : "Awaiting approval"}
                                            </p>
                                        </div>
                                    </div>
                                    <Badge variant={formData.isKycApproved ? "default" : "secondary"}
                                        className={formData.isKycApproved ? "bg-green-100 text-green-800" : "bg-gray-100 text-gray-700"}>
                                        {formData.isKycApproved ? "Approved" : "Pending"}
                                    </Badge>
                                </div>

                                {!formData.isKycSubmitted && (
                                    <Button className="w-full medical-gradient text-white hover:opacity-90">
                                        Submit KYC Documents
                                    </Button>
                                )}
                            </div>
                        </CardContent>
                    </Card>
                </div>
            </div>
        </div>
    );
};

export default PatientProfile;
